html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 1.5rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 97%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor-main {
  width: 97%;
  max-width: 120rem;
  margin: 0 auto;
  min-height: 100vh;
  position: relative;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: block;
}

.default-img {
  margin: 0 auto;
  z-index: 10;
  min-width: 30rem;
}

.default-contenedor {
  max-width: 100%;
  margin: 0 auto;
  max-height: 50rem;
  overflow: hidden;
}

.fxElement {
  opacity: 0;
  transition: all 0.5s;
}

.fxMostrarArriba {
  animation: fxMostrarArriba 1s;
}

@keyframes fxMostrarArriba {
  0% {
    transform: translateY(6rem);
  }
  100% {
    transform: translateY(0);
  }
}
.root2-img {
  margin: 0 auto;
  z-index: 10;
  height: 100%;
  width: 100%;
  outline: none;
  padding: 20rem;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  filter: brightness(30%);
}

.inicio_info1_servicios-cards {
  display: grid;
  padding: 1rem;
}
@media (min-width: 768px) {
  .inicio_info1_servicios-cards {
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
  }
}

.root-socialIcons {
  position: relative;
  margin: 0 auto;
  margin-top: -4rem;
}
@media (min-width: 1200px) {
  .root-socialIcons {
    left: 20%;
  }
}

.palabras-del-equipo {
  display: grid;
}
@media (min-width: 768px) {
  .palabras-del-equipo {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.palabras-del-equipo h4 {
  font-size: 3rem;
}

.palabras-del-equipo_foto {
  max-width: 40rem;
  max-height: 50rem;
  margin: 0 auto;
}
.palabras-del-equipo_foto img {
  border-radius: 30%;
  background-color: #ebc246;
}

.palabras-del-equipo_parrafo-principal {
  font-style: italic;
  text-align: justify;
  line-height: 2;
}

.palabras-del-equipo_firma {
  width: 30rem;
  display: grid;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.palabras-del-equipo_firma .palabras-del-equipo_firma_titulo {
  color: gray;
  padding-top: 2rem;
}

.palabras-del-equipo_firma-firma {
  width: 10rem;
  height: 15rem;
}

.info-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.2s;
}
.info-card:hover {
  transform: scale(1.03);
}
.info-card .info-card_icon {
  color: #1ab5e6;
}
.info-card .info-card_titulo {
  text-align: center;
}
.info-card .info-card_titulo h2 {
  font-size: 1.7rem;
  font-weight: 700;
  margin-bottom: 0;
}
.info-card .info-card_texto p {
  font-size: 1.4rem;
  line-height: 1.7;
}
.info-card .info-card_texto p span {
  font-weight: 700;
}

.pagelayer-shape-fill {
  fill: #FFFFFF;
}

.pagelayer-svg {
  width: 100%;
  height: 6rem;
}

.footer {
  background-color: #1ab5e6;
  height: 8rem;
  width: 100%;
}

.footer_content {
  height: 8rem;
  width: 80%;
  display: grid;
  align-items: center;
  /* justify-content:left; */
  margin: 0 auto;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
}
@media (min-width: 768px) {
  .footer_content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30%;
  }
}
.footer_content p {
  margin: 0;
  color: #FFFFFF;
  text-align: center;
  padding-left: 0rem;
}
@media (min-width: 768px) {
  .footer_content p {
    padding-left: 3rem;
  }
}
.footer_content p span {
  font-weight: 700;
  font-size: 1.8rem;
  color: #ebc246;
}

.header {
  background-color: #1ab5e6;
  display: block;
}

.header-img {
  display: block;
  height: 100%;
  width: 100%;
  outline: none;
  padding: 15rem;
  z-index: 1;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
}

.socialIcons {
  max-width: 40rem;
  margin: 0 auto;
  display: flex;
  gap: 4rem;
  justify-content: center;
  z-index: 1;
}
@media (min-width: 768px) {
  .socialIcons {
    /* margin-right: 13%; */
    /* padding-bottom: 2rem; */
  }
}

.social-icon {
  transition-property: top;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  position: relative;
  top: 0rem;
  color: #ebc246;
}
.social-icon:hover {
  top: -0.7rem;
  transition-property: top;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.active {
  font-weight: bold;
  text-decoration: underline;
}

.header__nav {
  /* position: fixed; */
  width: 100%;
  display: flex;
  background-color: #1ab5e6;
  color: #FFFFFF;
  justify-content: center;
  padding-top: 2.5rem;
  height: 6rem;
  /* margin: 0 0 1rem 0; */
}
.header__nav .header__web-tittle {
  display: none;
  font-size: 2.8rem;
  padding-left: 10rem;
  margin: 0;
  color: #ebc246;
}
@media (min-width: 768px) {
  .header__nav .header__web-tittle {
    display: block;
  }
}
@media (min-width: 768px) {
  .header__nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.header__nav .header__links {
  display: flex;
  gap: 2rem;
  justify-content: center;
  font-size: 1.4rem;
  z-index: 1;
}
@media (min-width: 768px) {
  .header__nav .header__links {
    grid-column: 2/3;
    gap: 4rem;
    justify-content: left;
  }
}
.header__nav .header__links .header__links__link {
  color: #FFFFFF;
  text-transform: uppercase;
  transition-property: color;
  transition-duration: 1s;
  transition-timing-function: ease-out;
}
.header__nav .header__links .header__links__link:hover {
  cursor: pointer;
  transition-property: color;
  transition-duration: 1s;
  transition-timing-function: ease-out;
  color: #ebc246;
}

@media (max-width: 480px) {
  .header__nav .header__links__link {
    /* display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: 2; */
    height: 7rem;
    align-items: center;
    justify-items: center;
    gap: 1rem;
  }
}
.simple-text_info {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 1rem;
}
.simple-text_info h1 {
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  margin-bottom: 0;
  font-size: 2.5rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .simple-text_info h1 {
    font-size: 4rem;
    margin-top: 7rem;
  }
}
.simple-text_info h3 {
  text-transform: uppercase;
  margin-bottom: 0;
  margin-top: 12rem;
}

.error-page {
  background-color: #1ab5e6;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.error-page h1 {
  color: #ebc246;
  font-size: 13rem;
}
.error-page h2 {
  color: #FFFFFF;
  font-size: 3rem;
  font-weight: 700;
}
.error-page p {
  color: #FFFFFF;
  font-size: 3rem;
}

.error-page__redirigir {
  font-size: 1.9rem;
  transition: transform 0.2s;
}
.error-page__redirigir button {
  border: 0;
  color: #FFFFFF;
  background-color: #ebc246;
  height: 5rem;
  font-weight: 700;
}
.error-page__redirigir button:hover {
  box-shadow: 7px 6px 8px #1888aa;
  transform: scale(1.03);
}